    <cell-group class="form-field">
      <field rows="4" v-model="opi"   type="textarea" :disabled="true"></field>
    </cell-group>
    <div class="location">
      <img src="../../assets/images/position.png" alt="">
      <span>贺州市八步区太白西路100号</span>
