</template>

<script>
  import TitleBar from '@/components/titleBar'
  import {CellGroup,Field,Button} from 'vant';
  import {useRouter} from 'vue-router';
  export default {
      name: "detail",
      components:{
        TitleBar,
        CellGroup,
        Field,
        Button
      },
      props: {
          opinion: {
            type: String,
            default: '八步的太白路路中间那里的井盖又没了！这个井盖已经消失多次，请严肃处理！'
          }
      },
      setup(props){
          const router = useRouter();
          const goBack = () => router.go(-1);
          return {
               opi:props.opinion,
               goBack
          }
      }
  }
</script>

